import "core-js/modules/es.array.push.js";
import { ElMessage } from "element-plus";
import { mapMutations } from "vuex";
export default {
  name: "TrainParamController",
  data() {
    return {
      dialogVisible: false,
      form: {
        model_name: '',
        isNondimensionalize: true,
        nondimensionalizeType: '标准化',
        //文字转数字
        //大小值转化数组
        feature_range_max: 1,
        feature_range_min: 0,
        isDimensionReduction: true,
        dimensionReductionType: 'PCA',
        //文字转数字
        pca_components: 0.95,
        threshold: 0,
        estimatorType: 'KNN算法',
        n_neighbors: '5',
        //空格分割

        alpha: 1.0,
        criterion: 'gini',
        max_depth: 0,
        //空字符等于null
        random: 0,
        //空字符等于null

        n_estimators: "",
        //空格分割
        forest_criteria: 'gini',
        forest_max_depth: 0,
        forest_bootstrap: true
      }
    };
  },
  computed: {
    feature_range() {
      return [this.form.feature_range_min, this.form.feature_range_max];
    }
  },
  methods: {
    ...mapMutations(["createModel", 'setCurrentPage']),
    onSubmit() {
      try {
        if (this.form.model_name.trim() === "") {
          throw "模型名称不能为空";
        }
        if (this.form.estimatorType === "朴素贝叶斯" && this.form.nondimensionalizeType === "标准化" || this.form.estimatorType === "朴素贝叶斯" && this.form.feature_range_min < 0) {
          throw "朴素贝叶斯仅支持归一化，且最小值大于0";
        }
        if (this.form.nondimensionalizeType === "归一化" && this.form.feature_range_min > this.form.feature_range_max) {
          throw "归一化，最大值需要大于最小值";
        }
        let submitObj = JSON.parse(JSON.stringify(this.form));

        // 归一化标准化转换
        submitObj["nondimensionalizeType"] = submitObj["nondimensionalizeType"] === '标准化' ? 0 : 1;
        // 降维
        submitObj["dimensionReductionType"] = submitObj["dimensionReductionType"] === 'PCA' ? 0 : 1;
        //KNN个数
        submitObj["n_neighbors"] = submitObj["n_neighbors"].trim().split(" ").map(Number);
        submitObj["n_estimators"] = submitObj["n_estimators"].trim().split(" ").map(Number);
        submitObj["max_depth"] = submitObj["max_depth"] === 0 ? null : submitObj["max_depth"];
        submitObj["random"] = submitObj["random"] === 0 ? null : submitObj["random"];
        submitObj["forest_max_depth"] = submitObj["forest_max_depth"] === 0 ? null : submitObj["forest_max_depth"];
        switch (submitObj["estimatorType"]) {
          case "KNN算法":
            {
              submitObj["estimatorType"] = 0;
            }
            break;
          case "朴素贝叶斯":
            {
              submitObj["estimatorType"] = 1;
            }
            break;
          case "决策树":
            {
              submitObj["estimatorType"] = 2;
            }
            break;
          case "随机森林":
            {
              submitObj["estimatorType"] = 3;
            }
            break;
        }
        submitObj["feature_range"] = this.feature_range;
        submitObj["model_type"] = Number.parseInt(this.$route.query.model_type);

        // console.log(submitObj)
        this.createModel(submitObj);
        this.$router.push({
          name: 'homepage'
        });
        this.setCurrentPage(0);
      } catch (e) {
        ElMessage.error(e);
      }
    },
    confirmSubmit() {
      this.dialogVisible = true;
    },
    goBack() {
      this.$router.back();
    }
  }
};